<template>
  <div id="home">
      <page-header title="首页">
        <div slot=left @click="menuModal.state=true">
          <i class="fa fa-navicon"></i>
        </div>
        <div slot=right>
          <i class="fa fa-plus"></i>
        </div>
      </page-header>

      <div class="page-container">
        <van-row>
          <van-col span="12" 
            class="book-item"
            v-for="(book,i) in books"
            :key="i"
          >
            <book-card :book="book"></book-card>
          </van-col>
        </van-row>
        
      </div>

      <van-popup v-model="menuModal.state"
        position="left"
        :style="{
          width:'60%',
          height:'100%'
        }"
      >
        <van-cell title="我的收藏" is-link url="/collection" />
        <van-cell title="离线下载" is-link url="/download" />
        <van-cell title="历史记录" is-link to="/history" />
      </van-popup>
  </div>
</template>

<script>
import pageHeader from '@/components/common/PageHeader'
import BookCard from '@/components/common/BookCard';
import {fetchData} from '@/api'
export default {
    data(){
      return {
        books:[],
        menuModal:{
          state:false
        }
      }
    },
    components:{
      pageHeader,
      BookCard
    },
    mounted(){
      fetchData({
        url:'/api/Book/search',
        keyword:''
      },res => {
        let books = [];
        for(var k in res.data.data){
          if(!isNaN(parseInt(k))){
            books.push(res.data.data[k])
          }
        }
        this.books = books
      })
    }
}
</script>

<style lang="less">
#home{
  margin-top:50px;
}
.page-container{
  .book-item{
    padding:10px;
  }
  .book-item:nth-of-type(odd){
    padding-right:0 ;
  }
}
</style>


