<template>
    <div id="bookDetail">
        <page-header
            :title="'详情'"
            @onClickLeft="$router.go(-1)"
        >
            <div slot="left" >
                <i class="fa fa-chevron-left"></i>
            </div>
            <div slot="right" >
                <i class="fa fa-gear" @click="readerSettingModal.state = true"></i>
                <i class="fa fa-list-ul" @click="menuModal.state = true"></i>
            </div>
        </page-header>
        <div id="area"></div>

        <div class="page-footer">
            <van-row>
                <van-col span="12" class="page-footer-btn" @click="prevPage">上一页</van-col>
                <van-col span="12" class="page-footer-btn" @click="nextPage">下一页</van-col>
            </van-row>
        </div>

        <van-popup v-model="readerSettingModal.state"
            position="bottom"
            :style="{
                height:'30%',
                width:'100%'
            }"
            :overlay-style="{
                background:'transparent'
            }"
        >
            <div class="reader-setting-items">
                <div class="reader-setting-item">
                    <div>背景颜色</div>
                    <div class="color-items">
                        <div v-for="color in backgroundColors" :key="color"
                            class="color-item"
                            :class="color === selectedBC ? 'active' : ''"
                            :style="{backgroundColor:color}"
                            @click="selectedBC = color"
                        ></div>
                    </div>
                </div>
                <div class="reader-setting-item">
                    <div>字体颜色</div>
                    <div class="color-items">
                        <div v-for="color in colors" :key="color"
                            class="color-item"
                            :class="color === selectedColor ? 'active' : ''"
                            :style="{backgroundColor:color}"
                            @click="selectedColor = color"
                        ></div>
                    </div>
                </div>
                <div class="reader-setting-item">
                    <div>字体大小</div>
                    <div class="fontsize-items">
                        <div class="fontsize-item" 
                            v-for="item in fontSizes" 
                            :key="item.type"
                            :class="selectedFontSize === item.type ? 'active' :''"
                            :style="{
                                fontSize:parseInt(item.value) / 37.5 + 'rem'
                            }"
                            @click="selectedFontSize = item.type"
                        >
                            A
                        </div>
                    </div>
                </div>
            </div>
        </van-popup>
        <van-popup v-model="menuModal.state"
            position="right"
            :style="{
                height:'100%',
                width:'60%'
            }"
        >
            <div v-for="(item, k) in navigation" :key="k" @click="locationToPage(item)">
                <van-index-anchor :index="item.label" />
                <van-cell 
                    :title="subItem.label" 
                    v-for="(subItem,i) in item.subItem" 
                    :key="i" 
                    @click="locationToPage(item)"
                />
            </div>
        </van-popup>
    </div>
</template>

<script>
import pageHeader from '@/components/common/PageHeader'
export default {
    data(){
        return {
            book:null,
            displayed:null,
            rendition:null,
            navigation:null, // 目录
            locations:null, //导航
            bookAvailable:false, //是否已经解析完毕
            contents:[],
            readerSettingModal:{
                state:false
            },
            menuModal:{
                state:false
            },
            backgroundColors:['#FF00FF','#FFFF00','#00FFFF'],
            colors:['#0000FF','#FF0000','#00FF00'],
            fontSizes:[
                {title:'小',type:'sm',value:'14px'},
                {title:'中',type:'mid',value:'18px'},
                {title:'大',type:'lg',value:'22px'}
            ],
            selectedBC:'#FF00FF',
            selectedColor:'#0000FF',
            selectedFontSize:'mid'
        }
    },
    computed:{
        themes(){
            const arr = [];
            this.backgroundColors.forEach((bc,i) => {
                
                this.colors.forEach((c,j)=>{
                    let obj = {
                        name:'theme' + i +'' +  j,
                        style:{
                            body:{
                                color:c,
                                background:bc
                            }
                        }
                    }
                    arr.push(obj)
                })
            })
            console.log(arr);
            return arr;
        }
    },
    watch:{
        selectedFontSize(){
            this.changeFontSize();
        },
        selectedBC(){
            this.changeTheme()
        },
        selectedColor(){
            this.changeTheme()
        }
    },
    methods:{
        locationToPage(item){
            const { href,id } = item;
            this.rendition.display(href);
            this.menuModal.state = false;
        },
        prevPage() {
            if (this.book) {
                console.log(this.rendition);
                this.rendition.prev()
            }
        },
        nextPage() {
            if (this.rendition) {
                this.rendition.next()
            }
        },
        changeFontSize(){
            let fontSize = '';
            this.fontSizes.map(item => {
                if(item.type == this.selectedFontSize){
                    fontSize = item.value
                }
            })
            this.rendition.themes.fontSize(fontSize);
        },
        changeTheme(){
            this.themes.some(theme => {
                if( theme.style.body.color == this.selectedColor 
                    && theme.style.body.background == this.selectedBC
                ){
                    this.rendition.themes.select(theme.name);
                    return true;
                }
            })
        }
    },
    mounted(){
        const {paramsId} = this;
        let bookUrl = '';
        //

        if(this.paramsId == 'local'){
            bookUrl = localStorage.getItem('localBookUrl');

        }else{
            let book = JSON.parse(sessionStorage.getItem('book'));
            bookUrl = book.url;
        }



        this.book = ePub({ restore: true }); 
        this.book.open(bookUrl);
        // this.book = ePub("https://nenyes.oss-cn-shenzhen.aliyuncs.com/yuedu/renjian.epub");
        this.rendition = this.book.renderTo("area", {width: '100%', height: '100%',margin:0});
        this.displayed = this.rendition.display();
        
        this.book.ready.then(() => {
            // 生成目录
            this.navigation = this.book.navigation.toc
            // 生成Locations对象
            return this.book.locations.generate()
        }).then(result => {
            // 保存locations对象
            this.locations = this.book.locations
            // 标记电子书为解析完毕状态
            this.bookAvailable = true
        })
        this.themes.forEach(theme => {
            this.rendition.themes.register(theme.name, theme.style);
        })
        this.rendition.themes.select(this.themes[0].name);
        console.log(this.athemes);
    },
    components:{
        pageHeader
    }
}
</script>

<style lang="less">
#bookDetail{
    height:100%;
    padding-top:46px;
    padding-bottom:46px;
    box-sizing: border-box;
}
#area{
    width:100%;
    height:100%;
}
#bookDetail .page-footer{
    position:fixed;
    width:100%;
    left:0;
    bottom:0;
    height:46px;
    font-size:16px;
    background: #1976d2;
    .page-footer-btn{
        text-align: center;
        color:#fff;
        padding-top:12px;
    }
}
#bookDetail .reader-setting-item{
    padding:10px;
}
.color-items,.fontsize-items{
    display: flex;
}
.fontsize-items{
    justify-content: space-around;
    align-items: flex-end;
}
.color-item{
    width: 20px;
    height:20px;
    border-radius: 20px;
    margin:2px 6px;
    border:2px solid transparent;
}
.color-item.active{
    border-color: #1976d2;
}
.fontsize-item.active{
    color:#1976d2;
}
</style>


