import axios from 'axios';
import qs from 'qs';
const PROTOCAL = window.location.protocol;
const HOST = window.location.host;

const PATH = PROTOCAL + '//' + HOST;

export let AJAXPATH = '';

if(PATH == 'http://localhost:8080' || PATH == 'http://localhost:8081'){
    AJAXPATH = '/api';
}else{
    AJAXPATH = PATH;
}

export const  basePath =  (AJAXPATH == '/api' ? 'http://121.41.97.126' : AJAXPATH )

var instance = axios.create({
    timeout: 3000
})

instance.interceptors.request.use(config => {
    return config;
})

axios.interceptors.response.use( res => {
    // 对响应数据做点什么
    return response;
}, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
});

export const fetchData = async( params, callback ) => {
    const { data } = await instance({
        url: AJAXPATH +  params.url,
        method:'POST',
        data: qs.stringify(params),
        withCredentials:true,
        headers:{
            'Content-Type':'application/x-www-form-urlencoded',
            datasources:'mobile',
            ...params.headers
        }
    })
    if(callback){
        callback(data);
    }else{
        return data;
    }
}



