<template>
  <div class="pageHeader">
    <van-nav-bar
        :fixed="true"
        @click-left="$emit('onClickLeft')"
        @click-right="$emit('onClickRight')"
    >
        <div slot="left" class="page-header-left"><slot name="left"></slot></div>
        <div slot="title" class="page-header-title">{{title}}</div>
        <div slot="right" class="page-header-right">
            <slot name="right"></slot>
        </div>
    </van-nav-bar>
  </div>
</template>

<script>
export default {
    props:{
        title:{
            type:String,
            default:'标题'
        }
    }
}
</script>

<style lang="less">
.pageHeader .van-nav-bar{
    background-color: #1976d2;
    border-color: #1976d2;
    color:#fff;
    .van-nav-bar__title,
    .van-nav-bar__text,
    .van-icon {
        color:#fff
    }
    .van-nav-bar__title{
        max-width:78%;
    }
}

.page-header-title{
    text-align: left;
}
</style>


