<template>
  <div class="book-card" @click="onBookCardClick">
      <figure class="book-card-img"><img :src="book.extImg" alt=""></figure>
     
      <div class="book-card-title">{{book.name}}</div>
      <!-- <small class="book-card-des">这是一本书</small> -->
      <div class="book-card-btns">
          <i class="fa fa-star"></i>
      </div>
  </div>
</template>

<script>
export default {
    props:{
        book:{
            type:Object
        }
    },
    methods:{
        onBookCardClick(){
            sessionStorage.setItem('book',JSON.stringify({
                id:this.book.id,
                title: this.book.name,
                url:this.book.bookUrl
            }))
            this.$router.push('/bookdetail/' + this.book.id)
        }
    }
}
</script>

<style>
.book-card{
    font-size:16px;
    border-radius:10px;
    overflow: hidden;
    box-shadow: 0 4px 5px rgba(0,0,0,.15);
    /* margin:10px; */
}
/* .book-card:nth-of-type(odd){
    margin-right:0px;
} */
.book-card-img{
    width:100%;
    height:200px;
    margin:0;
    overflow: hidden;
}
</style>


